.hero__subtitle img{
  width: 10.3rem;
  height: 10.3rem;
}
.hero__content h1{
    font-size: 3.2rem;
    font-weight: 600;
    margin-top: 1rem;
    margin-bottom: 2rem;
    color: var(--heading-color);
}
.hero__content h1 .highlight{
    color: var(--secondary-color);
}
.hero__content p{
    font-size: 1.1rem;
    color: var(--text-color);
    line-height: 2rem;

}
.hero__image-box img, .hero__image-box video{
    width: 100%;
    height: 350px;
    border-radius: 20px;
    border: 1px solid var(--secondary-color);
    object-fit: cover;
}
.hero__content{
    padding-top: 3.5rem;

}
.hero__image-box{
    padding-top: 2rem;
}
.services__subtitle{
    font-style: 2.5rem;
    font-weight: 1000;
    color: #ee6e6e;
    font-family: var(--subtitle-font-name);

}
.services__title{
    font-style: 2.8rem;
    font-weight: 500;
}
.featured__tour-title{
    font-size: 2.3rem;
    font-weight: 500;
}
.experience__content h2{
    font-size: 2.5rem;
     margin-top: 5rem;
     font-weight: 500;

}
.experience__content p{
    font-size: 1.1rem;
    color: var(--text-color);
    margin-top: 1rem;

}
.counter__wrapeer{
    margin-top: 2.5rem;
}
.counter__box span{
    width: 70px;
    height: 70px;
    margin: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    background: var(--primary-color);
    color: #fff;
    font-size: 1.6rem;
    font-weight: 600;
    border-radius: 10px 5px 10px 5px;
}
.counter__box h6{
    font-size: 1.1rem;
    margin-top: 0.7rem;
    color: var(--text-color);

}
.experience__img{
    width: 90%;

}
.gallery__title{
    font-size: 2.3rem;
    margin-bottom: 2.5rem;
    margin-top: 1rem;
}
.masonry__img{
    transition: .3s;

}
.masonry__img:hover{
    transform: scale(1.1);
}
.testimonial__title{
    font-size: 2.3rem;
    margin-top: 1rem;

}